import Vue from 'vue'
import config from '@/api/default_service.config'
import BottomBar from '../../components/BottomBar'
import ScreenUtil from '../../utils/ScreenUtil'
import {queryHomeSlide, queryTopStrategy, queryTopUser, queryNews, queryRecommendStock} from '../../api/API'
import { Swipe, SwipeItem } from 'mint-ui'
Vue.component(Swipe.name, Swipe)
Vue.component(SwipeItem.name, SwipeItem)

let timer
let stopeTimer = false

export default {
  name: 'home',
  data () {
    return {
      hasBg: false,
      selectedTab: 0,
      slides: [],
      stocks: [],
      strategies: [],
      topUsers: [],
      newsList: [],
      baseUrl: window.location.host.indexOf('localhost') > -1 ? config.baseURL_TEST : config.baseURL
    }
  },
  components: {
    BottomBar
  },
  mounted () {
    ScreenUtil.bindScroll(window, this.testHeader)
    this.loadSlide()
    this.loadStocks()
    this.loadTopStrategy()
    this.loadTopUser()
    this.loadNews()
  },
  beforeDestroy () {
    ScreenUtil.unBindScroll(window, this.testHeader)
    stopeTimer = true
    if (timer !== null) {
      clearInterval(timer)
      timer = null
    }
  },
  methods: {
    updataIndexs () {
      this.$store.dispatch('Stock/updateMarketIndex')
    },
    async loadSlide () {
      let res = await queryHomeSlide(this.$store.state.User.token)
      if (res.code === 200) {
        this.slides = res.data
      }
    },
    async loadTopStrategy () {
      let res = await queryTopStrategy(this.$store.state.User.token)
      if (res.code === 200) {
        this.strategies = res.data.page_data
      }
    },
    async loadTopUser () {
      let res = await queryTopUser(this.$store.state.User.token)
      if (res.code === 200) {
        this.topUsers = res.data.page_data
      }
    },
    async loadNews () {
      let res = await queryNews(this.$store.state.User.token)
      if (res.code === 200) {
        this.newsList = res.data.page_data
      }
    },
    async loadStocks () {
      let res = await queryRecommendStock(this.$store.state.User.token)
      if (res.code === 200) {
        this.stocks = res.data.page_data
      }
      if (stopeTimer === false) {
        this.nextLoop()
      }
      this.updataIndexs()
    },
    nextLoop () {
      timer = setTimeout(this.loadStocks, 5000)
    },
    testHeader () {
      if (ScreenUtil.getScrollY() > 200 * document.documentElement.clientWidth / 375) {
        this.hasBg = true
      } else {
        this.hasBg = false
      }
    },
    onNewsTabItemClicked (selectedIndex) {
      this.selectedTab = selectedIndex
    },
    showNews (id) {
      window.EventBus.$emit('show_news', id)
    },
    goUrl (url) {
      if (url.indexOf('http') > -1) {
        window.location.href = url
      } else {
        let parse = url.split('_')
        if (parse[0] === 'news') {
          window.EventBus.$emit('show_news', parse[1])
        } else {
          this.$router.push({path: url})
        }
      }
    }
  }
}



